import { LoadingController, ToastController } from '@ionic/angular';
import { Component, OnInit } from '@angular/core';
import { AngularFireAuth } from '@angular/fire/auth';
import { AngularFirestore, AngularFirestoreDocument, AngularFirestoreCollection } from '@angular/fire/firestore';
import { Observable, } from 'rxjs'
import { map } from 'rxjs/operators';
import { ActivatedRoute, Router, NavigationExtras } from '@angular/router'
export interface Item { name: string; }
import { UserService } from './../../SERVICES/user.service';


@Component({
  selector: 'app-bene-compte',
  templateUrl: './bene-compte.page.html',
  styleUrls: ['./bene-compte.page.scss'],
})
export class BeneComptePage implements OnInit {


  posts: any;
  title: string;
  content: string;






  loading: any;



  constructor(
    public loadingController: LoadingController,
    public router: Router,
    private route: ActivatedRoute,

    public afAuth: AngularFireAuth,
    private firestore: AngularFirestore,
    public toastController: ToastController,
    public user: UserService


  ) { }

  async GetPrivateInfo() {

    // get document
    this.presentLoading('Pollenisation du profil')
    console.log(this.user.getUID())
    const docRef = this.firestore.collection('privatebene').doc(this.user.getUID());
    docRef.get().pipe(
      map(doc => doc.data())
    )
      .subscribe(data => {
        console.log(data)
        console.log(data.name)
        if (data.name ) { this.dataUser.name= data.name; }
        console.log(this.dataUser.name)
        if (data.certifs ) { this.dataUser.certifs = data.certifs; }
        console.log(this.dataUser.certifs)
        if (data.motivations) { this.dataUser.motivations = data.motivations; }
        console.log(this.dataUser.motivations)
        if (data.skills) { this.dataUser.skills = data.skills; }
        console.log(this.dataUser.skills)
        if (data.pprofil) { this.dataUser.logocrop = data.pprofil; }
        this.deleteLoading()
      })
  }

  ngOnInit() { }
  ionViewWillEnter() { this.GetPrivateInfo() }

  // data to pass to edit page
  dataUser = {
    logocrop: '',
    name: '',
    motivations: '',
    certifs: '',
    skills: '',
    logo: ''
  };

  // go to edit and pass dataUser
  Goedit() {

    let navigationExtras: NavigationExtras = {
      state: {
        dataUser: this.dataUser
      }
    }
    this.router.navigate(['bene-edit-compte'], navigationExtras);
  }

  logout() {
    this.afAuth.signOut();
    this.router.navigate(['welcome']);
  }

  // ALERTS //////////
  async presentLoading(message: string) {
    this.loading = await this.loadingController.create({ message })
    return this.loading.present();
  }

  async deleteLoading() {
    const loader = this.loadingController.getTop();
    (await loader).parentNode.removeChild(await loader);

  }




}
